import './src/style.scss'

/**
 * @name 
* @param { String } url []
 */

const protocol = (url = '') => {
  try {
    if (typeof url !== 'string') return url
    if (!url) return ''
    return url.replace(/^http(s?):/, '')
  } catch (e) {
    console.log(e)
    return url
  }
}

export default protocol



