import './src/style.scss'

/**
 * @name [](https://cloud.tencent.com/document/product/275/3807#8-.E5.9B.BE.E5.83.8F.E5.A4.84.E7.90.86)
 * @param  { String } src 
 * @param  { Number, String } width 
 * @param  { Number, String } height 
 * @param  { String } type 
 * @param  { Number, String } way 0-5
 * @param  { Number, String } quality  85
 */

const imageoptimizer = (src = '', width, height, type, way = 1, quality = 85) => {
  try {
    if (typeof src !== 'string') return src
    if (!src) return ''
    if (!/image\.myqcloud\.com/.test(src)) return src
    src = src.split('?')[0]
    if (way >= 0 && way <= 5) {
      src = `${src}?imageView2/${way}`
      if (width) {
        src = `${src}/w/${width}`
      }
      if (height) {
        src = `${src}/h/${height}`
      }
      src = type ? `${src}/q/${quality}/format/${type}` : `${src}/q/${quality}`
    } else {
      src = `${src}?imageMogr2/${way}`
    }
    return src
  } catch (e) {
    console.log(e)
    return src
  }
}

export default imageoptimizer



