import './src/style.scss'

import fetch from 'isomorphic-fetch'
import queryString from 'query-string'

/*
 * @private
 * [fetch]
 * @param  {[String]} url     []
 * @param  {[Object]} data    [param]
 * @param  {[Object]} options []
 * e.g: [String]method, [Object]headers, [String]credentials
 * @return {[Object]}      [json]
 */

export default async (url = '', data = {}, options = {}) => {
  try {
    const reqUrl = Object.keys(data).length > 0 ? `${url}?${queryString.stringify(data)}` : url

    /*
     * method
     * credentials
     * headers
     * body GET  POST 
     * mode
     * etc.
     */
    const op = {
      method: 'GET', // GET / POST
      credentials: 'omit', // omit:  cookies; include:  cookies (); same-origin: URL cookies
      ...options,
    }

    const res = await fetch(reqUrl, op)
    const json = await res.json()
    return json
  } catch (e) {
    throw new Error(e)
  }
}



