<template>
  <input class="rjcv-upload" type="file" :accept="accept" :capture="capture" :multiple="isMultiple" @change="onChange">
</template>

<script>
import exif from '../../__assets/js/exif.js'

export default {
  name: 'rjcv-upload',
  props: {
    // ,  image/*,  video/*, audio/* , ,  `image/*, video/*`
    accept: {
      type: String,
      default: 'image/*'
    },
    // ,  false,  camera , camcorder , microphone 
    capture: {
      type: [Boolean, String],
      default: false
    },
    // , , multiple  capture,  multiple  true 
    isMultiple: {
      type: Boolean,
      default: true
    },
    // 
    disabled: {
      type: Boolean,
      default: false
    },
    // , ,  n , ,  n-1 , , 
    rest: {
      type: Number,
      default: 3
    },
    // ,  20 * 1024 * 1024 B 20 M
    maxSize: {
      type: Number,
      default: 20971520
    },
    // , , `bmp|gif|jp(e?)g|png|webp|mp4`
    fileRegExp: {
      type: String,
      default: 'bmp|gif|jp(e?)g|png|webp'
    },
    // 
    maxWidth: {
      type: Number,
      default: 800
    },
    // ,  0-1
    quality: {
      type: Number,
      default: 0.7
    },
    // 
    imageUploadUrl: {
      type: String,
      default: '//ims.cekid.com/pic/file/upload.do?bucket=wxmallpic'
    },
    //  10000ms
    timeout: {
      type: Number,
      default: 10000
    },
    // 
    signatureUrl: {
      type: String,
      default: '//appdata.cekid.com/vvideo/upload?app=h5&source=shequ'
    },
    // appid 
    appid: {
      type: [Number, String],
      default: '10004025'
    },
    // bucket 
    bucket: {
      type: String,
      default: 'shequvideo'
    },
    // region   gz  sh  tj
    region: {
      type: String,
      default: 'sh'
    },
    // ,  (reload && reload.progress === 0 && reload.fd) , ,  (reload && reload.progress === 0 && reload.videoFd) , 
    reload: {
      type: Object,
      default () {
        return {}
      }
    },
  },
  data () {
    return {
    }
  },
  watch: {
    reload (reload) {
      if (reload && reload.progress === 0) {
        if (reload.fd) {
          this.uploadImage(reload)
        } else if (reload.videoFd) {
          this.uploadVideo(reload)
        }
      }
    },
  },
  methods: {
    response (data) {
      this.$emit('uploadResponse', data)
    },
    async onChange (e) {
      try {
        if (typeof FileReader === 'undefined') {
          this.response({
            code: 6,
            data: {
              state: '',
            },
            message: '',
          })
        } else {
          const files = [...e.target.files]
          let fileArray = files.slice(0, this.rest)
          const total = fileArray.length
          if (!total) return
          const fileRegExp = new RegExp(this.fileRegExp, 'i')
          fileArray.forEach((file, index) => {
            const { lastModified, name, size, type, webkitRelativePath } = file
            const _file = {
              id: +new Date() + index, // 
              index, // 
              lastModified, // 
              name, // 
              size, // 
              total, // 
              type, // 
              webkitRelativePath, // 
            }
            if (size > this.maxSize) {
              this.response({
                code: 7,
                data: {
                  ..._file,
                  state: '',
                },
                message: '',
              })
            } else if (fileRegExp.test(type)) {
              if (/image/i.test(type)) {
                // 
                this.readAsDataURL(file, _file, 1)
              } else if (/video/i.test(type)) {
                // 
                this.readAsDataURL(file, _file, 2)
              }
            } else {
              // 
              this.response({
                code: 8,
                data: {
                  ..._file,
                  state: '',
                },
                message: '',
              })
            }
          })
          //  input 
          e.target.value = null
        }
      } catch (e) {
        this.response({
          code: 0,
          data: {
            state: '',
          },
          message: '',
        })
        console.log(e)
      }
    },
    readAsDataURL (file, _file, classify) {
      try {
        const reader = new FileReader()
        reader.onload = (event) => {
          _file.classify = classify
          _file.src = event.target.result
          this.response({
            code: 1,
            data: {
              ..._file,
              state: '',
            },
            message: '',
          })
          if (classify === 1) {
            // 
            this.imageProcess(file, _file)
          } else if (classify === 2) {
            // 
            const videoFd = new FormData()
            videoFd.append('op', 'upload')
            videoFd.append('fileContent', file)
            videoFd.append('insertOnly', 0)
            _file.videoFd = videoFd
            this.uploadVideo(_file)
          }
        }
        reader.onerror = (e) => {
          this.response({
            code: 9,
            data: {
              ..._file,
              state: '',
            },
            message: '',
          })
          console.log(e)
        }
        reader.readAsDataURL(file)
      } catch (e) {
        this.response({
          code: 0,
          data: {
            ..._file,
            state: '',
          },
          message: '',
        })
        console.log(e)
      }
    },
    imageProcess (file, _file) {
      try {
        const img = new Image()
        img.src = _file.src
        img.onload = () => {
          // 
          const width = img.width
          const height = img.height
          _file.width = width
          _file.height = height

          //  canvas 
          const canvas = document.createElement('canvas')
          canvas.width = width
          canvas.height = height
          const ctx = canvas.getContext('2d')
          ctx.fillStyle = '#fff'
          ctx.fillRect(0, 0, width, height)

          //  exif
          const imgObj = {
            src: _file.src
          }
          exif.getData(imgObj, () => {
            _file.exifdata = imgObj.exifdata
            //  Orientation 
            const orientation = imgObj.exifdata.Orientation
            // 1 , 3 , 6 , 8 
            if (orientation && orientation !== 1) {
              switch (orientation) {
                case 8:
                  // 8 , 90 
                  canvas.width = height
                  canvas.height = width
                  ctx.rotate(-0.5 * Math.PI)
                  ctx.translate(-1 * width, 0)
                  break
                case 6:
                  // 6 , 90 
                  canvas.width = height
                  canvas.height = width
                  ctx.rotate(0.5 * Math.PI)
                  ctx.translate(0, -1 * height)
                  break
                case 3:
                  // 3 , 180 
                  ctx.rotate(Math.PI)
                  ctx.translate(-1 * width, -1 * height)
                  break
                default:
                  ctx.rotate(0)
              }
            }
            ctx.drawImage(img, 0, 0, width, height, 0, 0, width, height)

            // 
            const w = Math.min(canvas.width, this.maxWidth)
            const h = canvas.height / canvas.width * w
            // 
            _file.zoomWidth = w
            _file.zoomHeight = h
            // 
            const _canvas = document.createElement('canvas')
            _canvas.width = w
            _canvas.height = h
            const _ctx = _canvas.getContext('2d')
            _ctx.fillStyle = '#fff'
            _ctx.fillRect(0, 0, w, h)
            _ctx.drawImage(canvas, 0, 0, canvas.width, canvas.height, 0, 0, w, h)

            //  webp|jpeg
            let type
            if (/webp/.test(_file.type)) {
              type = 'image/webp'
            } else {
              type = 'image/jpeg'
            }
            // canvas to dataURL
            _file.preview = _canvas.toDataURL(type, this.quality)
            _file.progress = 0

            // dataURL 
            this.response({
              code: 2,
              data: {
                ..._file,
                state: ''
              },
              message: '',
            })

            setTimeout(() => {
              // dataURL to blob
              this.dataURL2Blob(_file)
            }, 0)
          })
        }
        img.onerror = (e) => {
          this.response({
            code: 10,
            data: {
              ..._file,
              state: ''
            },
            message: '',
          })
          console.log(e)
        }
      } catch (e) {
        this.response({
          code: 0,
          data: {
            ..._file,
            state: '',
          },
          message: '',
        })
        console.log(e)
      }
    },
    dataURL2Blob (_file) {
      try {
        // base64 to buffer
        let byteStr
        const parts = _file.preview.split(',')
        if (parts[0].indexOf('base64') > 0) {
          byteStr = atob(parts[1])
        } else {
          byteStr = decodeURIComponent(parts[1])
        }
        const leng = byteStr.length
        const buffer = new ArrayBuffer(leng)
        const uint8 = new Uint8Array(buffer)
        for (let i = 0; i < leng; i += 1) {
          uint8[i] = byteStr.charCodeAt(i)
        }
        const type = parts[0].split(':')[1].split(';')[0]

        // buffer to blob
        let blob
        const Builder = window.BlobBuilder || window.WebKitBlobBuilder
        if (Builder) {
          // android  new Blob,  window.BlobBuilder || window.WebKitBlobBuilder
          const bb = new Builder()
          bb.append(buffer)
          blob = bb.getBlob(type)
        } else {
          // ios
          blob = new Blob([buffer], type ? { type } : {})
        }

        if (blob) {
          const fd = new FormData()
          fd.append('file', blob, _file.name)
          _file.fd = fd
          this.uploadImage(_file)
        } else {
          this.response({
            code: 5,
            data: {
              ..._file,
              state: '',
            },
            message: '',
          })
        }
      } catch (e) {
        this.response({
          code: 0,
          data: {
            ..._file,
            state: '',
          },
          message: '',
        })
        console.log(e)
      }
    },
    uploadImage (_file) {
      try {
        const xhr = new XMLHttpRequest()
        xhr.timeout = this.timeout
        xhr.responseType = 'json'
        xhr.open('POST', this.imageUploadUrl)
        if (/mobile/i.test(navigator.userAgent)) {
          //  progress
          _file.progress = 0.5
          this.response({
            code: 3,
            data: {
              ..._file,
              state: ''
            },
            message: '',
          })
        } else {
          // PC  onprogress
          xhr.upload.onprogress = (e) => {
            if (e.lengthComputable) {
              _file.progress = e.loaded / e.total
              this.response({
                code: 3,
                data: {
                  ..._file,
                  state: ''
                },
                message: '',
              })
            }
          }
        }
        xhr.onload = (e) => {
          const { status, response } = e.target
          if ((status >= 200 && status < 300) || status === 304) {
            _file.res = response
            _file.progress = 1
            this.response({
              code: 4,
              data: {
                ..._file,
                state: ''
              },
              message: '',
            })
          } else {
            _file.res = null
            _file.progress = 0
            this.response({
              code: 5,
              data: {
                ..._file,
                state: ''
              },
              message: '',
            })
          }
        }
        xhr.onerror = (e) => {
          _file.res = null
          _file.progress = 0
          this.response({
            code: 5,
            data: {
              ..._file,
              state: ''
            },
            message: '',
          })
          console.log(e)
        }
        xhr.ontimeout = (e) => {
          _file.res = null
          _file.progress = 0
          this.response({
            code: 5,
            data: {
              ..._file,
              state: ''
            },
            message: '',
          })
          console.log(e)
        }
        xhr.send(_file.fd)
      } catch (e) {
        _file.res = null
        _file.progress = 0
        this.response({
          code: 0,
          data: {
            ..._file,
            state: '',
          },
          message: '',
        })
        console.log(e)
      }
    },
    async uploadVideo (_file) {
      try {
        const getAuthorization = new XMLHttpRequest()
        getAuthorization.responseType = 'json'
        getAuthorization.open('GET', this.signatureUrl)
        getAuthorization.onload = (e) => {
          const { status, response } = e.target
          if ((status >= 200 && status < 300) || status === 304) {
            const authorization = response.data && response.data.authorization
            if (authorization) {
              const xhr = new XMLHttpRequest()
              xhr.timeout = this.timeout
              xhr.responseType = 'json'
              xhr.open('POST', `//${this.region}.file.myqcloud.com/files/v2/${this.appid}/${this.bucket}/${_file.name}?sign=${encodeURIComponent(authorization)}`)
              if (/mobile/i.test(navigator.userAgent)) {
                //  progress
                _file.progress = 0.5
                this.response({
                  code: 3,
                  data: {
                    ..._file,
                    state: ''
                  },
                  message: '',
                })
              } else {
                // PC  onprogress
                xhr.upload.onprogress = (e) => {
                  if (e.lengthComputable) {
                    _file.progress = e.loaded / e.total
                    this.response({
                      code: 3,
                      data: {
                        ..._file,
                        state: ''
                      },
                      message: '',
                    })
                  }
                }
              }
              xhr.onload = (e) => {
                const { status, response } = e.target
                if ((status >= 200 && status < 300) || status === 304) {
                  _file.res = response
                  _file.progress = 1
                  this.response({
                    code: 4,
                    data: {
                      ..._file,
                      state: ''
                    },
                    message: '',
                  })
                } else {
                  _file.res = null
                  _file.progress = 0
                  this.response({
                    code: 5,
                    data: {
                      ..._file,
                      state: ''
                    },
                    message: '',
                  })
                }
              }
              xhr.onerror = (e) => {
                _file.res = null
                _file.progress = 0
                this.response({
                  code: 5,
                  data: {
                    ..._file,
                    state: ''
                  },
                  message: '',
                })
                console.log(e)
              }
              xhr.ontimeout = (e) => {
                _file.res = null
                _file.progress = 0
                this.response({
                  code: 5,
                  data: {
                    ..._file,
                    state: ''
                  },
                  message: '',
                })
                console.log(e)
              }
              xhr.send(_file.videoFd)
            } else {
              _file.res = null
              _file.progress = 0
              this.response({
                code: 5,
                data: {
                  state: '',
                },
                message: '',
              })
            }
          } else {
            _file.res = null
            _file.progress = 0
            this.response({
              code: 5,
              data: {
                state: '',
              },
              message: '',
            })
          }
        }
        getAuthorization.onerror = (e) => {
          _file.res = null
          _file.progress = 0
          this.response({
            code: 5,
            data: {
              state: '',
            },
            message: '',
          })
          console.log(e)
        }
        getAuthorization.send()
      } catch (e) {
        _file.res = null
        _file.progress = 0
        this.response({
          code: 0,
          data: {
            ..._file,
            state: '',
          },
          message: '',
        })
        console.log(e)
      }
    },
  },
}
</script>

<style lang="scss">
@import './style.scss';
</style>



